.main {
  width: 100wh;
  display: flex;
  flex-direction: column;
  margin-top: 100px;
}

.btn {
  padding: 20px;
  background-color: darkolivegreen;
  color: white;
  font-weight: 700;
  width: 200px;
  margin: auto;
  text-align: center;
  border-radius: 10px;
  margin-bottom: 50px;
}

.btn:hover {
 background-color: #fff;
 border: 1px solid darkolivegreen;
 color: darkolivegreen;
 cursor: pointer;
}

.data {
  margin-top: 30px;
  width: 100wh;
  margin: auto;
}